<template lang="html">
  <div v-if="section"
    class="section-title-container w3-leftbar gray-1-border">

    <div class="w3-row title-row">
        <div class="section-title cursor-pointer w3-left">
          {{ section.title }}
        </div>
    </div>

    <div class="also-in-container">
      <div class="w3-row">
        <div class="w3-left">
          <i>in:</i>
        </div>
        <div v-for="parentSection in section.inSections" class="in-tag-container w3-left">
          <div class="gray-1 w3-tag w3-round w3-small">
            {{ parentSection.title }}
          </div>
        </div>
      </div>
    </div>

    <div v-if="expand && section.description" class="w3-row gray-1-border section-description w3-small">
      <vue-markdown class="marked-text" :source="section.description" :anchorAttributes="{target: '_blank'}"></vue-markdown>
    </div>

  </div>
</template>

<script>
export default {
  props: {
    section: {
      type: Object,
      defaul: null
    },
    inElementId: {
      type: String,
      default: ''
    },
    expand: {
      type: Boolean,
      default: true
    },
    forceUpdate: {
      type: Boolean,
      default: false
    }
  },

  computed: {
    sectionIsInOtherPlaces () {
      var ix
      for (ix in this.section.inSections) {
        if (this.section.inSections[ix].id !== this.inElementId) {
          return true
        }
      }

      return false
    }
  }

}
</script>

